<template>
  <div class="buy-page">
    <navigation-bar
      :pageName="'立即购买'"
      :isShowBack="true"
      :isDefault="true"
      @onLeftClick="onBackClick"
    >
    </navigation-bar>

    <div class="buy-page-content">
      <!-- 商品信息 -->
      <div class="buy-page-content-goods">
        <img class="buy-page-content-goods-img" :src="goodsData.img" />
        <div class="buy-page-content-goods-desc">
          <p class="buy-page-content-goods-desc-name">
            {{ goodsData.name }}
          </p>
          <p class="buy-page-content-goods-desc-price">
            ￥{{ goodsData.price }}
          </p>
        </div>
      </div>
      <!-- 支付方式 -->
      <ul class="buy-page-content-list">
        <li
          class="buy-page-content-list-item"
          v-for="(item, index) in payData"
          :key="index"
          @click="onPaymentItemClick(item)"
        >
          <img
            class="buy-page-content-list-item-icon"
            :src="item.icon"
            alt=""
            srcset=""
          />
          <div class="buy-page-content-list-item-desc">
            <p class="buy-page-content-list-item-desc-name">{{ item.name }}</p>
            <p class="buy-page-content-list-item-desc-desc">{{ item.desc }}</p>
          </div>
          <img
            class="buy-page-content-list-item-check"
            :src="checkIcon(item)"
            alt=""
            srcset=""
          />
        </li>
      </ul>
    </div>
    <!-- 购买按钮 -->
    <div class="pay page-commit" @click="onPayClick">立即购买</div>
  </div>
</template>

<script>
import NavigationBar from "@c/currency/NavigationBar";
import { useRouter, useRoute } from "vue-router";
export default {
  name: "BuyComponents",
  components: {
    NavigationBar,
  },
  data() {
    return {
      goodsData: [],
      //支付方式数据源
      payData: [
        {
          id: "1",
          name: "支付宝支付",
          desc: "数亿用户都在用，安全可托付",
          icon: require("@img/alipay.svg"),
        },
        {
          id: "2",
          name: "微信支付",
          desc: "亿万用户选择，更快更安全",
          icon: require("@img/weichat.png"),
        },
      ],
      //选中的支付方式
      selectPayment: {
        id: "0",
      },
    };
  },
  setup() {
    const router = useRouter(),
      route = useRoute();
    //获取数据
    const handleGoods = () => {
      const goods = route.query.goods;
      return goods;
    };
    const onBackClick = () => {
      router.go(-1);
      // router.push({
      //   name: "goodsList",
      // path: "/buy",
      // query: {
      //   goods: obj,
      //   routerType: "",
      // },
      // });
    };

    return { handleGoods, onBackClick };
  },
  created() {
    this.goodsData = JSON.parse(this.handleGoods());
  },
  methods: {
    //读取选中的项目
    onPaymentItemClick(item) {
      this.selectPayment = item;
    },
    //返回对应的选中图片
    checkIcon(item) {
      // return this.checkImg.noCheck;
      if (item.id === this.selectPayment.id) {
        return require("@img/check.svg");
      } else {
        return require("@img/no-check.svg");
      }
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@css/style.scss";
.buy-page {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: $bgColor;
  &-content {
    height: 100%;
    height: 100%;

    //商品信息
    &-goods {
      background-color: white;
      padding: $marginSize;
      display: flex;
      border-bottom: px2rem(1) solid $lineColor;
      border-top: px2rem(1) solid $lineColor;
      margin-top: $marginSize;
      &-img {
        width: px2rem(100);
        height: px2rem(100);
      }
      &-desc {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 0 $marginSize;

        &-name {
          font-size: $infoSize;
          line-height: px2rem(18);
        }

        &-price {
          font-size: $titleSize;
          color: $mainColor;
          font-weight: 500;
        }
      }
    }

    //支付方式
    &-list {
      margin-top: $marginSize;
      &-item {
        display: flex;
        background-color: white;
        border-bottom: 1px solid $lineColor;
        padding: $marginSize;
        align-items: center;
        &-icon {
          height: px2rem(30);
        }

        &-desc {
          margin-left: px2rem(12);
          flex-grow: 1;
          &-name {
            font-size: $infoSize;
          }

          &-desc {
            font-size: $minInfoSize;
            margin-top: px2rem(6);
            color: $textHintColor;
          }
        }

        &-check {
          width: px2rem(16);
          height: px2rem(16);
        }
      }
    }
  }
  .pay {
    position: absolute;
    bottom: px2rem(52);
  }
}
</style>
